html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  overflow-x: hidden;
  position: relative;
  min-height: 100vh;
}
html.no-scroll {
  overflow: hidden !important;
  height: 100% !important;
}
html.no-scroll body {
  overflow: hidden !important;
  height: 100vh !important;
}
html.no-scroll-long {
  overflow-y: scroll !important;
  position: static;
}

::-webkit-scrollbar {
  width: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(157, 157, 157, 0.7019607843);
  border-radius: 1rem;
}

body {
  font-family: "Manrope", sans-serif;
  font-size: 1.6rem;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.theme-light {
  background-color: #ffffff;
}

img,
picture {
  width: 100%;
  height: auto;
  display: block;
}

h1 {
  font-size: 6.4rem;
  font-weight: bold;
}

h2 {
  font-size: 2.4rem;
  font-weight: normal;
}

p {
  font-size: 1.8rem;
  line-height: 1.5;
}

h1, h2, p {
  color: #202020;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor-padding {
  padding: 0 2.5rem;
}
@media (min-width: 480px) {
  .contenedor-padding {
    padding: 0 5rem;
  }
}
@media (min-width: 768px) {
  .contenedor-padding {
    padding: 0 10rem;
  }
}
@media (min-width: 1080px) {
  .contenedor-padding {
    padding: 0 20rem;
  }
}

a {
  text-decoration: none;
  font-size: 1.6rem;
}
@media (min-width: 480px) {
  a {
    font-size: 1.8rem;
  }
}

#contenido {
  transition: 0.5s ease-out;
  margin-bottom: 15rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.w-100 {
  width: 100%;
}

.margen-cero {
  margin: 0;
}

.blur {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}

.no-background {
  box-shadow: none !important;
}

.ocultar {
  display: none !important;
}

.mover-Y {
  transform: translateY(100vh);
}

.titulo {
  font-size: 3.6rem;
  text-align: center;
  padding: 10rem 0 4rem;
}

.theme-dark {
  background-color: #202020;
}
.theme-dark .barra {
  box-shadow: 0 0.1rem 1rem rgba(50, 50, 50, 0.7019607843);
}
@media (min-width: 768px) {
  .theme-dark .barra {
    background-color: rgba(0, 0, 0, 0.4784313725);
  }
}
.theme-dark .barra .logo svg g {
  fill: #DEDEDE;
}
.theme-dark .header .div-theme {
  background-color: #FBD38D;
}
.theme-dark .header .div-theme:hover {
  background-color: #f9c05c;
}
.theme-dark .header .nav-telefono {
  border: 1px solid #DEDEDE;
}
.theme-dark .header .nav-telefono.activo {
  background-color: #202020;
}
.theme-dark .bubbles svg g path {
  fill: #bbbbff;
  stroke: #bbbbff;
  opacity: 0.1;
}
.theme-dark .nav-desktop a {
  color: #DEDEDE;
}
.theme-dark .nav-desktop a::after,
.theme-dark .seccion-proyectos .contenedor-proyecto::after,
.theme-dark .nav-telefono .linea {
  background-color: #DEDEDE;
}
.theme-dark h1,
.theme-dark h2,
.theme-dark .contenido-principal .nombre,
.theme-dark .dev {
  color: #DEDEDE;
}
.theme-dark p,
.theme-dark span,
.theme-dark .dev span,
.theme-dark .trabajos-destacados,
.theme-dark .trabajos-destacados svg {
  color: rgba(222, 222, 222, 0.8509803922);
  stroke: rgba(222, 222, 222, 0.8509803922);
}
.theme-dark .boton-contacto {
  background-color: #5858a5;
  color: #ffffff;
}
.theme-dark .boton-contacto svg {
  stroke: #ffffff;
}
.theme-dark .seccion-proyectos .contenedor-proyecto .contenido-proyecto {
  background-color: #323232;
}
.theme-dark .seccion-proyectos .contenedor-proyecto .imagen-proyecto a {
  background-color: #bbbbff;
}
.theme-dark .seccion-proyectos .ver-proyectos a {
  color: #ffffff;
}
.theme-dark .seccion-proyectos .ver-proyectos a::after {
  background-color: #5858a5;
}
.theme-dark .contenido-proyecto .tecnologias-proyecto {
  color: #bbbbff;
}
.theme-dark .footer p {
  color: #ffffff;
}
.theme-dark .footer .redes-sociales ul a {
  color: #ffffff;
}
.theme-dark .iconos-contacto li:nth-child(1) svg,
.theme-dark .contacto-iconos li:nth-child(1) svg {
  stroke: none;
}
.theme-dark .iconos-contacto li svg,
.theme-dark .contacto-iconos li svg {
  fill: #ffffff;
}
.theme-dark .iconos-contacto::after,
.theme-dark .iconos-contacto.activo::before {
  background-color: #ffffff;
}
.theme-dark .iconos-proyecto svg {
  stroke: #ffffff;
}
.theme-dark .flecha-inicio {
  background-color: #464673;
}
.theme-dark .flecha-inicio svg {
  stroke: #DEDEDE;
}
.theme-dark .contenido-skills .contenedor-skills span {
  border: solid 1px #DEDEDE;
}
.theme-dark .contenedor-informacion .imagen-mau {
  background-color: #323232;
}
.theme-dark .contenedor-informacion .info-mau a {
  color: #ff937e;
}

.portafolio .seccion-proyectos {
  padding: 0;
}
@media (min-width: 768px) {
  .portafolio .seccion-proyectos {
    gap: 5rem;
  }
}

.pagina-contacto strong {
  font-size: 2rem;
}
.pagina-contacto .contenido-contacto {
  max-width: 90rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 768px) {
  .pagina-contacto .contenido-contacto {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding: 8rem 0;
  }
}

@media (min-width: 768px) {
  .contacto-email {
    grid-column: 1/7;
    text-align: start;
  }
}

.contacto-media {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
@media (min-width: 768px) {
  .contacto-media {
    grid-column: 7/13;
    text-align: end;
    justify-content: space-between;
  }
}
.contacto-media .contacto-iconos ul {
  display: flex;
  justify-content: center;
  list-style: none;
  gap: 4rem;
}
.contacto-media .contacto-iconos ul li {
  transition: 0.25s ease-out;
}
.contacto-media .contacto-iconos ul li:nth-child(1) svg {
  fill: #0a66c2;
}
.contacto-media .contacto-iconos ul li:nth-child(2) svg {
  fill: #b72f8a;
}
.contacto-media .contacto-iconos ul li:hover {
  transform: translateY(-0.75rem);
}
.contacto-media .contacto-iconos ul svg {
  height: 3.5rem;
  width: 3.5rem;
}
@media (min-width: 768px) {
  .contacto-media .contacto-iconos ul {
    justify-content: flex-end;
  }
}

.contenedor-informacion {
  max-width: 90rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-informacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    align-items: center;
  }
  .contenedor-informacion div:nth-child(3),
.contenedor-informacion div:last-of-type {
    grid-column: 1/3;
    padding: 8rem 0;
  }
}
.contenedor-informacion .intro-info {
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor-informacion .intro-info {
    text-align: start;
  }
}
.contenedor-informacion .intro-info p {
  font-size: 2.2rem;
  margin-bottom: 1rem;
}
.contenedor-informacion .intro-info .nombre {
  margin: 0;
  line-height: 1.1;
}
.contenedor-informacion .imagen-mau {
  background-color: #DEDEDE;
  border-radius: 1rem;
  max-height: 40rem;
  max-width: 40rem;
}
.contenedor-informacion .info-mau a {
  font-size: 1.8rem;
  font-weight: 600;
  color: #a1311b;
}
.contenedor-informacion .info-mau a:hover {
  text-decoration: underline;
}
.contenedor-informacion .info-mau .subtitulo {
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.barra {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 2;
  transition: 0.5s ease-in-out;
  box-shadow: 0 0 1rem #202020;
}
@media (min-width: 768px) {
  .barra {
    background-color: rgba(222, 222, 222, 0.8509803922);
  }
}
.barra .logo {
  position: relative;
  min-width: 10rem;
  z-index: 1;
  display: flex;
  margin-right: 3rem;
  transition: 0.3s;
}
.barra .logo:hover {
  transform: scale(1.1);
}
.barra .logo g {
  fill: #202020;
}
.barra .logo a, .barra .logo svg {
  position: absolute;
  width: 100%;
  height: 100%;
}
.barra.ocultar {
  top: -7rem;
}

.header {
  display: flex;
  justify-content: space-between;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.header .div-theme {
  height: 4rem;
  min-width: 4rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #202020;
  border-radius: 1rem;
  cursor: pointer;
  background-color: #5858a5;
  z-index: 1;
  transition: 0.3s;
}
.header .div-theme:active {
  transform: translateY(15%);
  opacity: 0;
}
.header .div-theme:hover {
  background-color: #464684;
}
.header .mix-header {
  display: flex;
}

.button-theme {
  background-color: transparent;
  border-width: 0;
  cursor: pointer;
  max-height: 2.5rem;
}
.button-theme .light-mode,
.button-theme .dark-mode {
  padding: 0.2rem;
}
.button-theme .light-mode g {
  stroke: #202020;
}
.button-theme .dark-mode path {
  fill: #ffffff;
}

.nav-telefono {
  position: relative;
  height: 4rem;
  width: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #202020;
  border-radius: 1rem;
  gap: 0.15rem;
  margin-left: 1rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav-telefono {
    display: none;
  }
}
.nav-telefono .linea {
  height: 0.25rem;
  width: 2.3rem;
  background-color: #202020;
  border-radius: 0.5rem;
  transition: 0.3s;
}
.nav-telefono .linea:nth-child(1), .nav-telefono .linea:nth-child(3) {
  width: 1.5rem;
}
.nav-telefono.activo {
  background-color: #DEDEDE;
}
.nav-telefono.activo .linea {
  position: absolute;
}
.nav-telefono.activo .linea:nth-child(1), .nav-telefono.activo .linea:nth-child(3) {
  width: 2.3rem;
}
.nav-telefono.activo .linea:nth-child(1) {
  transform: rotate(45deg);
}
.nav-telefono.activo .linea:nth-child(2) {
  width: 0;
}
.nav-telefono.activo .linea:nth-child(3) {
  transform: rotate(-45deg);
}

.nav-desktop {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0);
  top: 0;
  left: 0;
  height: 0vh;
  width: 100%;
  transition: all 700ms ease;
  overflow: hidden;
}
.nav-desktop a {
  opacity: 0;
  transition: all 700ms ease;
  padding: 3rem 0;
  color: #202020;
  font-size: 3rem;
  font-weight: 700;
  text-align: center;
  transform: translateY(-100vh);
}
.nav-desktop a:first-of-type {
  margin-top: 15vh;
}
@media (min-width: 768px) {
  .nav-desktop {
    position: static;
    height: auto;
    background-color: transparent;
    flex-direction: row;
    align-items: center;
    transition: none;
    justify-content: flex-start;
  }
  .nav-desktop a {
    opacity: 1;
    margin: 0 1rem;
    transition: none;
    padding: 0;
    text-align: center;
    transform: translateY(0);
    font-size: 1.6rem;
  }
  .nav-desktop a::after {
    content: "";
    display: block;
    height: 0.1rem;
    width: 0%;
    background-color: #202020;
    position: absolute;
    bottom: -0.1rem;
    left: 0;
    transition: 500ms;
  }
}
@media (min-width: 768px) and (min-width: 1080px) {
  .nav-desktop a {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .nav-desktop a:first-of-type {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .nav-desktop a:hover::after {
    width: 100%;
  }
}
.nav-desktop.activo {
  height: 100vh;
}
.nav-desktop.activo a {
  transform: translateY(0);
  opacity: 1;
}
.nav-desktop.activo a:hover {
  opacity: 0.7;
}
@media (max-height: 480px) {
  .nav-desktop.activo a {
    padding: 1.5rem 0;
  }
  .nav-desktop.activo a:first-of-type {
    margin-top: 5rem;
  }
}

.contenido-principal {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 9rem 0;
}
@media (min-width: 768px) {
  .contenido-principal {
    padding-bottom: 0;
  }
}
@media (min-width: 1080px) {
  .contenido-principal {
    padding: 17vh 0 0;
  }
}
.contenido-principal h1 {
  font-size: 2.2rem;
  font-weight: 400;
}
@media (min-width: 480px) {
  .contenido-principal h1 {
    font-size: 2.4rem;
  }
}
@media (min-width: 1080px) {
  .contenido-principal h1 {
    font-size: 2.6rem;
  }
}
.contenido-principal p {
  font-size: 1.8rem;
  max-width: 80rem;
  margin-top: 2rem;
}
.contenido-principal p,
.contenido-principal .dev span {
  font-weight: 600;
}
@media (min-width: 480px) {
  .contenido-principal p,
.contenido-principal .dev span {
    font-size: 2rem;
  }
}
@media (min-width: 1080px) {
  .contenido-principal p,
.contenido-principal .dev span {
    font-size: 2.2rem;
  }
}
.contenido-principal span {
  color: #1a1919;
}

.nombre {
  font-size: 3.6rem;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
  color: #202020;
  display: block;
  margin-top: 1rem;
}
@media (min-width: 480px) {
  .nombre {
    font-size: 5.5rem;
  }
}
@media (min-width: 1080px) {
  .nombre {
    font-size: 6.4rem;
  }
}

